<template>
  <div class="login">
    <el-container>
      <el-header>
        <!-- <el-row>
                    <el-col :span='20'>
                    <el-menu class="el-menu-demo" mode="horizontal" text-color="#409EFF" background-color="#ffffff" style="border-bottom-color:#909399">
                    <el-menu-item index="1"><el-link href="http://localhost:8080/#/index" ><b>程序设计语言教程案例图示化演示系统</b></el-link></el-menu-item> -->
        <!-- <el-menu-item index="2"><el-link href="example" >课程</el-link></el-menu-item>
                    <el-menu-item index="3" ><el-link href="class" >班级</el-link></el-menu-item> -->
        <!-- </el-menu>
                    </el-col>
                    <el-col :span='4'>
                      <el-menu class="el-menu-demo" mode="horizontal" text-color="#000" background-color="#ffffff" style="border-bottom-color:#909399">
                      <el-menu-item index="4"><el-link href="http://localhost:8080/#/home" >身份认证中</el-link></el-menu-item>
                      </el-menu>
                    </el-col>
                  </el-row> -->
        <!-- <el-menu-item index="4"><a href="https://www.ele.me" >订单管理</a></el-menu-item> -->
      </el-header>
      <el-main>
        <el-row>
          <el-col :span="2">
            <div style="width: 200px; height: 600px">
              <!-- <el-image
                                style="width: 100px; height: 100px"
                                :src="headline"
                                :fit="fill"></el-image> -->
            </div>
          </el-col>
          <el-col :span="20">
            <el-card class="box-card" shadow="always">
              <div slot="header" class="clearfix">
                <span>欢迎来到网站 </span>
              </div>
              <div class="text item">
                <el-tabs
                  v-model="activeName"
                  type="card"
                  @tab-click="handleClick"
                >
                  <el-tab-pane label="登陆" name="first" label-width="80px">
                    <el-form ref="form" :model="form" label-width="80px">
                      <el-form-item label="用户名">
                        <el-input v-model="form.username"></el-input>
                      </el-form-item>
                      <el-form-item label="密码">
                        <el-input
                          v-model="form.password"
                          show-password
                        ></el-input>
                      </el-form-item>
                      <el-form-item>
                        <el-button
                          type="primary"
                          @click="onSubmit"
                          style="width: 100%"
                          >&nbsp;&nbsp;&nbsp;登陆&nbsp;&nbsp;&nbsp;</el-button
                        >
                      </el-form-item>
                    </el-form>
                  </el-tab-pane>
                </el-tabs>
              </div>
            </el-card>
          </el-col>
          <el-col :span="2"></el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      webname: "七夕特券",
      headline: "../static/login_head.png/",
      activeName: "first",
      form: {
        username: "",
        password: "",
        secpassword: "",
      },
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    onSubmit() {
      // var pas = this.getCookie(this.form.username);
      let pas = 20210814;
      console.log(pas, this.form.password);
      if (pas == this.form.password) {
        location.replace("/#/quan");
      } else {
        this.open("用户名或密码错误，请重新输入", "错误");
      }
    },
    onregister() {
      console.log(
        "resigter",
        this.form.username,
        this.form.password,
        this.form.secpassword
      );
      if (this.form.password === this.form.secpassword) {
        this.setCookie(this.form.username, this.form.password, 30);
        this.open("注册成功，请登陆", "成功");
        location.reload();
      } else {
        this.open("两次密码不一致，请重新输入", "错误");
      }
      this.form.password = "";
      this.form.secpassword = "";
    },
    open(info, infotitle) {
      this.$alert(info, infotitle, {
        confirmButtonText: "确定",
        callback: (action) => {
          this.$message({
            type: "info",
            message: info,
          });
        },
      });
    },
    setCookie(cname, cvalue, exdays) {
      var d = new Date();
      d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
      var expires = "expires=" + d.toGMTString();
      document.cookie = cname + "=" + cvalue + "; " + expires;
    },
    getCookie(cname) {
      var name = cname + "=";
      var ca = document.cookie.split(";");
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i].trim();
        if (c.indexOf(name) === 0) return c.substring(name.length, c.length);
      }
      return "";
    },
    // 下面是createcode函数
    checklogin() {
      document.title = "七夕特券";
    },
  },
  mounted: function () {
    this.checklogin(); // 需要触发的函数
  },
};
</script>
<link rel="stylesheet" href="https://unpkg.com/element-ui/lib/theme-chalk/index.css">;
<script src="https://unpkg.com/element-ui/lib/index.js"></script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
    color:black;
    background-color: rgb(255, 248, 220);
    text-align: left;
    }
.el-header, .el-footer {
    background-color: #409EFF;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
.el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
.haed{
    color:#409EFF
}

</style>



