      // console.log("val", val);
      this.quanname = val.name;
      this.quannum = val.num;
      this.pagenum = 1;
      this.quancount = val.count - val.use[0];
      this.descri = val.des;
      this.quanps = val.zhu;
      this.quntest = "";
      if (val.ID <= 22) {
        this.url = "./static/img/use/" + val.img + "0.png";
      } else {
        this.url = "./static/img/" + val.img + ".png";
      }
      console.log(this.url);
    },
    index() {
      this.show = false;
      this.show2 = true;
      this.show3 = false;
      this.quanname = "目录";
    },
    next() {
      if (this.page <= 22) {
        if (this.pagenum < 5) {
          let n = this.pagenum;
          this.pagenum = this.pagenum + 1;
          this.quancount =
            this.quanlist[this.page - 1].count -
            this.quanlist[this.page - 1].use[this.pagenum - 1];

          this.url =
            "./static/img/use/" + this.quanlist[this.page - 1].img + n + ".png";
          console.log(this.url, this.pagenum);
        } else {
          this.page = parseInt(this.page) + 1;
          this.quanname = this.quanlist[this.page - 1].name;
          this.quannum = this.quanlist[this.page - 1].num;

          this.quancount =
            this.quanlist[this.page - 1].count -
            this.quanlist[this.page - 1].use[this.pagenum - 1];
          this.pagenum = 1;
          this.descri = this.quanlist[this.page - 1].des;
