          if (this.page <= 22) {
            this.url =
              "./static/img/use/" + this.quanlist[this.page - 1].img + "0.png";
          } else {
            this.url =
              "./static/img/" + this.quanlist[this.page - 1].img + ".png";
          }
          console.log(this.url);
        }
      } else {
        this.page = parseInt(this.page) + 1;
        if (this.page > 29) {
          console.log("..");
          this.show = false;
          this.show2 = true;
          this.show3 = false;
          this.quanname = "目录";
        }
        this.quanname = this.quanlist[this.page - 1].name;
        this.quannum = this.quanlist[this.page - 1].num;
        this.pagenum = 1;
        this.quancount =
          this.quanlist[this.page - 1].count -
          this.quanlist[this.page - 1].use[this.pagenum - 1];
        this.descri = this.quanlist[this.page - 1].des;
        this.quanps = this.quanlist[this.page - 1].zhu;
        this.url = "./static/img/" + this.quanlist[this.page - 1].img + ".png";

        console.log(this.url);
      }
    },
    start() {
      this.show = false;
      this.show2 = true;
      this.show3 = false;

      axios
        // .get("/api/quanlist")
        .get("http://www.xbr520.com/quanlist")
        .then((response) => (this.quanlist = response.data))
        .catch(function (error) {
          // 请求失败处理
          console.log(error);
        });
    },
    use() {
      if (this.quancount > 0) {
        this.centerDialogVisible = true;
      } else {
        this.useout = true;
      }
    },
    useit() {
      this.quanlist[this.page - 1].use[this.pagenum - 1] =
        parseInt(this.quanlist[this.page - 1].use[this.pagenum - 1]) + 1;

      this.quancount = parseInt(this.quancount) - 1;

      let data = {
        name: this.quanname,
        num: this.pagenum - 1,
        count: this.quanlist[this.page - 1].use[this.pagenum - 1],
      };
      axios.post("http://www.xbr520.com/changelist", data).then((res) => {
        console.log("res=>", res);
      });

      axios
        .get("http://www.xbr520.com/quanlist")
        // .get("/api/quanlist")
        .then((response) => (this.quanlist = response.data))
        .catch(function (error) {
          // 请求失败处理
          console.log(error);
        });

      this.centerDialogVisible = false;
    },
  },
  mounted: function () {
    // this.checklogin(); // 需要触发的函数
  },
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<link rel="stylesheet" href="https://unpkg.com/element-ui/lib/theme-chalk/index.css">;
<script src="https://unpkg.com/element-ui/lib/index.js"></script>
<style scoped>
.login{
    color:black;
    background-color: rgb(255, 248, 220);
    text-align: left;
    }
.el-header, .el-footer {
    background-color: #409EFF;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
.el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
    .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
  .box-card {
    height: 200px;
  }
  .coursespic {
    background-color: darkgray;
    height: 150px;
    width: 200px;
  }
</style>



